@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@400;600&display=swap);
body {
  background-color: #f1f1f1;
}
.movie__title {
  display: flex;
  justify-content: left;

  font-size: 40px;
  padding: 0 25px 0 5px;
}
.movie__data {
  display: flex;
  justify-content: left;

  padding: 0 25px 0 5px;
}
.movie__title_font {
  border: solid 1px black;
  padding: 5px 30px 5px 30px;
}
.movie__eachInfo {
  border: solid 1px black;
  font-size: 30px;
  padding: 5px 30px 5px 30px;
}

.movie__rank {
  width: 200px;
  display: flex;
  justify-content: center;
}

.movie__name {
  width: 500px;
}
.movie__date {
  width: 300px;
  display: flex;
  justify-content: center;
}

.movie__rate {
  width: 180px;
  display: flex;
  justify-content: center;
}
.movie__cnt {
  width: 300px;
}

.movie_title {
  font-size: 30px;
  font-weight: 600;
}
.Menu-wrapper {
  position: fixed;
  top: 10%;
  left: 10%;
}
html,
body {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.homepage {
  width: 100%;
  height: 100%;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.homepage__header {
  position: fixed;
  top: 30px;
  left: 10%;
}

.homepage__title {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
.homepage__loader {
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loader__serchbar {
  margin-left: 15px;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul,
li {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}


